@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);

.App 
{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  height: 100%; 
}





:root {
        --color-gold: #B99362;
    }
body {
        background-color: #222;
    }
.App-map
	{
        height: 100vh;
        width: 100%;
        flex: 3 1;
	}

.App-map path 
	{
        stroke: white;
        fill: transparent;
        transition: 0.5s;
        cursor: pointer;
    }

.App-map path:hover 
	{
        fill: #B99362;
        fill: var(--color-gold);
        transform: translate(-5px, -5px);
    }



*{
	font-family: 'Fjalla One', sans-serif;
	
}

.title{
	position: absolute;
	right: 50px;
	top: 20px;
	color: white;
	font-size: 30px;
	
}

hr{
	margin-left: 0;
	width: 100px;
	border: none;
	border-bottom: solid 2px white;
}


